@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Fjalla One', sans-serif;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Badge__Background__Image {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Badge__Background__Image img {
  height: 500px;
}
.Badge {
  background: #FFFFFF;
  box-shadow: 8px 8px 8px 8px rgba(0, 0, 0, 0.10);
  border-radius: 8px 8px 8px 8px;
  overflow: hidden;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  max-width: 25rem;
  border: 1px solid #e5e5e5;
}

.Badge__header {
  padding: 0.5rem 0;
  height: 80px;
  /* background: #1B1B25; */
  display: flex;
  justify-content: center;
}

.Badge__header > img {
  object-fit: contain;
}

.Badge__section-name {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 0;
}

.Badge__section-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 0;
  background: #F4F4F7;
}


.Badge__avatar {
  border-radius: 50%;
  margin-right: 1rem;
}

.Badge__footer {
  height: 54px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #98CA3F;
  font-weight: bold;
  font-style: italic;
}

.Badge__section-age {
  display: flex;
  justify-content: center;
}
.Modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);

  display: flex;
  flex-direction: column;
  align-items: center;
}

.Modal__container {
  position: relative;
  top: 10rem;
  background-color: #ffffff;
  padding: 1rem;
  width: 400px;
}

.Modal__close-button {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  background-color: #F5F5F5;
  padding: 0.5rem 1rem;
}
.Users__hero {
  width: 100%;
  padding: 2rem 0;
  margin-bottom: 1rem;
}

.Users__hero>.Users__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Users__container {
  width: 100%;
  max-width: 580px;
  margin: 0 auto;
  padding: 0 1rem;
}

.Users_conf-logo {
  margin-bottom: 2rem;
}

.Users__logo {
  object-fit: contain;
}

.Users__logo {
  width: inherit;
}
.UserListItem {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
  margin-top: 1em;
  display: flex;
}

.UserListItem figure {
  margin: 15px;
}

.UserListItem__details {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.UserListItem__details p {
  margin: 0;
}

.UserListItem__fullname {
  font-weight: bold;
}

.UserListItem__twitter {
  color: rgb(27, 149, 224);
}

.UserListItem__jobTitle {
  font-size: small;
}

.UserListItem__twitter {
  display: flex;
  align-items: center;
}

.UserListItem__twitter img {
  height: 20px;
  margin-right: 5px;
}

.UserListItem__avatar {
  border-radius: 50%;
  margin-right: 1rem;
  width: 80px;
}

.BadgeList__empty {
  display: flex;
  flex-direction: column;
  align-items: center;
}

